import React, {useState} from 'react';
import Kids from './kids';
import Adults from './adult';
import Movielist from './movielist';



/// What should be the tiles for Halloween? Kick off of the festivities?



// var day1 = new Date(now);
// var day2 = new Date("10/31/2022");

// var difference= Math.abs(day2-day1);
// let days = difference/(1000 * 3600 * 24)



function Movies() {

  const[show,setShow]=useState(true);

  
    return (
<>
  <div className="col-md">
    <div className="border m-3">
      <h2 className='text-center'>Movies</h2>
      <ul>
      <button onClick={()=>setShow(!show)}>Toggle</button>
        <br></br>
         {show? (
          <>
        {Kids.map((e)=>{
       return (
       <Movielist title={e.title} link={e.link}/>
     );})}
     </>
     ) : ( 
      <>
        {Adults.map((e)=>{
       return (
       <Movielist title={e.title} link={e.link}/>
     );})}
</>
      )}
      </ul>
    </div>
    </div>
    {/* <div className={isActive ? "app" : null}>
      <h1>Hello react</h1>
      <button onClick={handleToggle}>Toggle class</button>
    </div> */}

    </>
    );
  };
    
export default Movies;

